@import "reset";
@import "css3";
@import "base";
@import "header_bar";
@import "loading";

.fade {
    visibility: hidden;
}

.report-preview {
    width: 78.5%;
    display: inline-block;
}

.report-thumb-container {
    background-color: #fff;
    border-bottom: 1px solid #ccc;
    & > img {
        width: 92px;
        float: left;
    }
    & > div {
        overflow: hidden;
        padding: 10px 30px;
        h2 {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
        }
        p.report-thumb-info {
            max-width: 50%;
            overflow: hidden;
            -ms-text-overflow: ellipsis;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: 10px;
            font-size: 14px;
        }
        p.download-count {
            font-size: 12px;
            color: #a8a8a8;
        }
    }
}

.report-ifr-container {
    width: 100%;
    height: 750px;
    overflow: hidden;
    background-color: #fff;
    iframe {
        width: 100%;
    }
}

.report-miss-container {
    position: relative;
    height: 400px;
    img {
        position: absolute;
        height: 60%;
        top: 50%;
        left: 50%;
        @include translate(-50%,-50%);
    }
}

.report-download-bar {
    height: 45px;
    background-color: #282828;
    position: relative;
    a {
        position: absolute;
        height: 66%;
        @include box-sizing(border-box);
        top: 50%;
        left: 50%;
        @include translate(-50%, -50%);
        font-size: 0;
        border: 2px solid #fff;
        border-radius: 20px;
        background-color: #282828;
        color: #fff;
        padding: 0 15px 1px;
        &::before {
            content: '\e900';
            font-family: 'icomoon';
            font-size: 24px;
            vertical-align: middle;
            display: inline-block;
            margin-right: 10px;
        }
        span {
            font-size: 15px;
            vertical-align: middle;
        }
        &::after {
            content: '.';
            width: 0;
            height: 100%;
            vertical-align: middle;
            display: inline-block;
            visibility: hidden;
        }
    }
}

.report-related {
    float: right;
    width: 20.5%;
    h2 {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 25px;
        text-align: center;
    }
}

.report-related-list {
    li {
        margin-bottom: 10px;
        & > a {
            display: block;
            &:hover {
                h3 {
                    text-decoration: underline;
                }
            }
        }
        img {
            width: 58px;
            float: left;
        }
        div {
            overflow: hidden;
            padding-left: 10px;
            h3 {
                overflow: hidden;
                -ms-text-overflow: ellipsis;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 14px;
                font-weight: bold;
                margin-bottom: 5px;
            }
            p {
                font-size: 12px;
                color: #909090;
            }
        }
        &::after {
            content: '';
            display: table;
            clear: both;
        }
    }
}